  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-30px);
  }

  60% {
    transform: translateY(-15px);
  }
}

.box {
  height: 150px;
  border-radius: 10px;
  transition: transform .5s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #0003;
}

