  width: 100%;
  height: 100%;
  object-fit: scale-down;
  object-position: center;
  transition: transform .5s;
}

.card-content {
  width: 100%;
  height: 100%;
  color: #fff;
  opacity: 0;
  box-sizing: border-box;
  background-color: #5988ffcc;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: 1rem;
  line-height: 1.5;
  transition: opacity .5s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card:hover img {
  transform: scale(1.1);
}

.card:hover .card-content {
  opacity: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Montserrat, sans-serif;
